import {useEffect, useState} from "react";
import {Link, NavLink, useLocation} from "react-router-dom";

function Header() {

    //Date
    let date = new Date().toLocaleDateString("ua", {
        weekday: "short",
        day: "numeric",
        month: "short",
    }).split(" ").map(e => e[0].toUpperCase() + e.slice(1)).join(" ");

    //Links
    const [links, setLinks] = useState([]);
    let location = useLocation();

    useEffect(() => {
        setLinks([localStorage.getItem("lastSeenGroup"), localStorage.getItem("lastSeenTeacher")])
    }, [location])

    return (
        <header className="header">
            <div className="header__container">
                <span className="header__logo">
                        KPISchedule 
                </span>
                <span className="header__links">
                    <NavLink className="links__item-link" activelassname='is-active'
                             to={`/groups/${localStorage.getItem("lastSeenGroup")}`}>
                        <span className="item-link__container">
                            <span className="item-link__emoji"></span>
                            <span className="item-link__name"></span>
                            <span className="item-link__decoration"/>
                        </span>
                    </NavLink>
                    <NavLink className="links__item-link" activeclassname='is-active'
                             to={`/teachers/${localStorage.getItem("lastSeenTeacher")}`}>
                        <span className="item-link__container">
                            <span className="item-link__decoration"/>
                            <span className="item-link__name"></span>
                            <span className="item-link__emoji"></span>
                        </span>
                    </NavLink>
                </span>
                <span className="header__date">
                    {date}
                </span>
            </div>
        </header>
    )
}

export default Header;