//React
import {useEffect, useState} from "react";
import {useLocation} from "react-router-dom";

//Moment
import moment from 'moment';
import 'moment/locale/uk';
//Swiper
import {Navigation, Pagination, Grid, Controller} from 'swiper';
import {Swiper, SwiperSlide} from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import "swiper/css/grid";
import 'swiper/css/scrollbar';
import ScheduleColumn from "./ScheduleColumn";

let lessonTimes = ["8:30", "10:25", "12:20", "14:15", "16:10", "18:30"]
let weeks = [" ", " "];

function Schedule({id, way, funMode, setIsLoaded}) {
    //Moment locale
    moment.locale('uk')
    let location = useLocation();

    //Get all weekdays with date;
    const startDay = moment().startOf('week').subtract(1, "day");
    const weekdays = [...Array(12)]
        .map(() => startDay.add(1, "day").day() !== 0 ? startDay.format("dd, D") : startDay.add(1, "day").format("dd, D"))
        .map(day => day[0].toUpperCase() + day.slice(1));


    //Schedule/Week state
    const [schedule, setSchedule] = useState({});
    const [week, setWeek] = useState("");
    const [filter, setFilter] = useState("")

    //Fetch Schedule/Week
    useEffect(() => {
        //fetchData function
        async function fetchData() {
            await setIsLoaded(false);

            let week = await fetch(`https://api.rozklad.org.ua/v2/weeks`)
                .then(res => res.json())
                .then(json => json.data)
                .then(data => +!(data - 1));
            setWeek(week)

            await fetch(`https://api.rozklad.org.ua/v2/${way}/${id}/lessons`)
                .then(res => res.json())
                .then(json => formatToArray(json.data, week))
                .then(data => {
                    fillEmpty(data);
                    findClosestLesson(data, week)
                    return data;
                }).then(data => setSchedule(data))
                .catch(() => setSchedule(emptySchedule));

            await setIsLoaded(true);
        }

        fetchData();
    }, [location, id])

    function emptySchedule() {
        let result = {}
        for (let i = 1; i <= 12; i++) {
            let lessons = [...Array(6)].map((lesson, index) => {
                return {
                    addArticles: "empty",
                    day_number: i,
                    lesson_full_name: "",
                    lesson_name: "",
                    lesson_number: index + 1,
                    lesson_type: "",
                    lesson_week: i <= 6 ? week : +!week,
                    rate: "",
                    teacher_name: "",
                }
            })
            result[i] = {
                day_number: i,
                week_number: i <= 6 ? week : +!week,
                lessons
            }
        }
        return result;
    }

    function formatToArray(data, week) {
        let result = {};
        data.forEach((lesson) => {
            let {day_number, lesson_week} = lesson;
            //lesson.addArticles = "";
            let currentDay = lesson_week - 1 === week ? +day_number : +day_number + 6;
            let lessonsArray = result[currentDay]?.lessons ? [...result[currentDay].lessons, lesson] : [lesson];
            result[currentDay] = {
                day_number: currentDay, week_number: lesson_week - 1, lessons: lessonsArray,
            }
        })

        if (Object.values(result).length < 12) {
            for (let i = 1; i <= 12; i++) {
                if (!result.hasOwnProperty(i)) {
                    result[i] = {
                        day_number: i,
                        week_number: i <= 6 ? week : +!week,
                        lessons: [],
                    }
                }
            }
        }
        return result;
    }

    function findClosestLesson(data, week) {
        let m = moment();
        let day = m.day();
        let isClosestFound = false;
        let days = Object.values(data);


        let currentDay = day;
        if (day === 0) {
            currentDay = 7;
            m = moment("0:00", "HH:mm");
        }

        for (let i = 0; i < days.length && !isClosestFound; i++) {
            let {day_number, week_number, lessons} = days[i];

            if (i === currentDay) m = moment("0:00", "HH:mm");

            if (week_number === week && day_number === currentDay && day !== 0) {
                lessons.forEach((lesson) => lesson.addArticles += " today-lesson")
            }
            let closest;
            if (day_number >= currentDay) {
                closest = lessons.find(({
                                            time_start,
                                            time_end
                                        }) => m.isBetween(moment(time_start, "HH:mm:ss"), moment(time_end, "HH:mm:ss")) || m.isBefore(moment(time_start, "HH:mm:ss")))

            }
            //Finish loop
            if (closest) {
                isClosestFound = true;
                closest.addArticles += " closest-lesson";
                break;
            }
        }
    }

    function fillEmpty(data) {
        let days = Object.values(data);
        days.forEach((day) => {
            let {lessons, day_number, week_number} = day;
            let lessonsObject = lessons.reduce(function (target, key) {
                target[key.lesson_number] = key;
                return target;
            }, {})

            for (let i = 1; i < 7; i++) {
                if (!lessonsObject.hasOwnProperty(i)) {
                    lessonsObject[i] = LessonObjectTemplate(i, day_number, week_number, days);
                }
            }
            day.lessons = Object.values(lessonsObject);
        })
    }

    function LessonObjectTemplate(lesson_number, day_number, lesson_week, days) {
        let customLesson = {
            addArticles: "empty-lesson",
            day_number,
            lesson_full_name: "",
            lesson_name: "",
            lesson_number,
            lesson_type: "",
            lesson_week,
            rate: "",
            teacher_name: "",
        }
        if (lesson_number != 1 && lesson_number != 6 && days[day_number - 1].lessons.length < 6) {
            let prevLesson = days[day_number - 1].lessons.some((lesson) => lesson.lesson_number < lesson_number/* && lesson.addArticles !== "empty" && lesson.addArticles !== "white"*/);
            let nextLesson = days[day_number - 1].lessons.some((lesson) => lesson.lesson_number > lesson_number /*&& lesson.addArticles !== "empty" && lesson.addArticles !== "white"*/);
            if (prevLesson && nextLesson) {
                customLesson.addArticles = " fun-mode-lesson";
                customLesson.lesson_full_name = " ";
                customLesson.lesson_name = " ";
            }
        }
        if (lesson_number == 1) {
            customLesson.addArticles = " fun-mode-lesson";
            customLesson.lesson_full_name = "   ";
            customLesson.lesson_name = "   ";
        }
        if (lesson_number == 1 && !days[day_number - 1].lessons.length) {
            customLesson.addArticles = " fun-mode-lesson";
            customLesson.lesson_full_name = " ";
            customLesson.lesson_name = " ";
            customLesson.lesson_number = "1";
        }
        if (lesson_number == 6 && (day_number == 6 || !days[day_number]?.lessons.length)) {
            customLesson.addArticles = " fun-mode-lesson";
            customLesson.lesson_full_name = "   ";
            customLesson.lesson_name = "   ";
        }
        return customLesson;
    }

    //Controlled Swiper
    const [weekSwiper, setWeekSwiper] = useState(null);
    const [scheduleSwiper, setScheduleSwiper] = useState(null);

    function scheduleSwiperHandler(realIndex) {
        if (realIndex >= 6 && weekSwiper) {
            weekSwiper.slideTo(1)
        } else if (realIndex < 6 && weekSwiper) {
            weekSwiper.slideTo(0)
        }
    }

    function weekSwiperHandler(realIndex, scheduleIndex) {
        if (realIndex === 0 && scheduleIndex >= 6) {
            scheduleSwiper.slideTo(0)
        } else if (realIndex === 1) {
            scheduleSwiper.slideTo(6)
        }
    }

    console.log(schedule);
    return (
        <div className="content__full-schedule">
            <div className="full-schedule__week">
                <Swiper
                    onSwiper={setWeekSwiper}
                    navigation={true}
                    modules={[Pagination, Navigation, Controller]}
                    onActiveIndexChange={({realIndex}) => {
                        weekSwiperHandler(realIndex, scheduleSwiper.realIndex);
                    }}>
                    <SwiperSlide><span className="week__name">{weeks[week]}</span></SwiperSlide>
                    <SwiperSlide><span className="week__name">{weeks[+!week]}</span></SwiperSlide>
                </Swiper>
            </div>
            <div className="full-schedule__table-container">
                <div className="full-schedule__time-column">
                    {
                        lessonTimes.map((time, index) =>
                            <div key={`time_${index}`} className="table__lesson-time">
                                <span>{time}</span>
                            </div>)
                    }
                </div>
                <div className="full-schedule__table ">
                    <Swiper
                        initialSlide={5}
                        onSwiper={setScheduleSwiper}
                        slidesPerView={6}
                        slidesPerGroup={6}
                        spaceBetween={5}
                        modules={[Grid, Pagination, Controller]}
                        onActiveIndexChange={({realIndex, previousIndex}) => {
                            scheduleSwiperHandler(realIndex, previousIndex);
                        }}
                        breakpoints={{
                            1440: {
                                slidesPerView: 6,
                                slidesPerGroup: 6
                            },
                            1024: {
                                slidesPerView: 3,
                                slidesPerGroup: 3
                            }, 600: {
                                slidesPerView: 2,
                                slidesPerGroup: 2
                            }, 0: {
                                slidesPerView: 1,
                                slidesPerGroup: 1,
                            }
                        }}>
                        {
                            Object.values(schedule).map(({day_number, lessons}, index) =>
                                <SwiperSlide key={index}>
                                    <ScheduleColumn
                                        filter={filter}
                                        setFilter={setFilter}
                                        weekday={weekdays[day_number - 1]}
                                        day={day_number}
                                        funMode={funMode}
                                        lessons={lessons}/>
                                </SwiperSlide>)
                        }
                    </Swiper>
                </div>
            </div>
        </div>)
}

export default Schedule;
