import AsyncSelect from "react-select/async";
import {useNavigate} from "react-router-dom";
import {useEffect, useState} from "react";

function ScheduleSelect({id, way}) {

    //State for Select
    const [loadedGroupsOptions, setLoadedGroupsOptions] = useState([]);
    const [defaultOptions, setDefaultOptions] = useState([]);
    const [defaultValue, setDefaultValue] = useState({});
    const [isLoading, setLoading] = useState(true);

    //Fetch data once for group options
    useEffect(() => {
        //Fetch Groups data
        async function fetchData() {
            let result = [];
            let offset = 0;
            while (true) {
                let currentData = await fetch(`https://api.rozklad.org.ua/v2/groups/?filter={"limit":100,"offset":${offset}}`)
                    .then(res => res.json())
                    .then(json => json.data);
                //Format Data for Select
                currentData = currentData.map(({group_full_name, group_id, group_prefix}) => {
                    if (group_id == id && way == "groups") {
                        let currentValue = {
                            label: `${group_full_name}`,
                            value: `${group_id}`,
                            prefix: `${group_prefix}`
                        };
                        setDefaultValue(currentValue)
                        setDefaultOptions([currentValue]);

                    }
                    return {
                        label: `${group_full_name}`,
                        value: `${group_id}`,
                        prefix: `${group_prefix}`
                    }
                })
                result = [...result, ...currentData];
                offset += 100;
                if (currentData.length < 100)
                    break;
            }
            setLoadedGroupsOptions(result)
            setLoading(false);
        }

        fetchData();
    }, [])

    //Update if location changes
    useEffect(() => {
        if (way == "groups") {
            setDefaultValue(loadedGroupsOptions[id - 1]);
            setDefaultOptions([loadedGroupsOptions[id - 1]]);
        } else if (way == "teachers") {
            async function getTeacher() {
                let teacher = {}
                await fetch(`https://api.rozklad.org.ua/v2/teachers/${id}`)
                    .then(res => res.json())
                    .then(json => json.data)
                    .then(({teacher_name, teacher_id}) => {
                        teacher.label = teacher_name
                        teacher.value = teacher_id
                    });
                setDefaultValue(teacher);
                setDefaultOptions([teacher]);
            }

            getTeacher();
        }
    }, [way, id])

    //Load options for AsyncSelect
    const loadOptions = (inputValue, callback) => {
        if (way == "groups") {
            return setTimeout(() => {
                callback(loadedGroupsOptions.filter((i) => {
                        if (inputValue.length === 2) {
                            return i.prefix.toLowerCase().includes(inputValue.toLowerCase())
                        } else if (inputValue.length > 2) {
                            return i.label.toLowerCase().includes(inputValue.toLowerCase())
                        }
                    })
                )
            });
        } else if (way == "teachers") {
            async function FetchData() {
                let result = [];
                if (inputValue.length > 2) {
                    await fetch(`https://api.rozklad.org.ua/v2/teachers/?search={'query': '${inputValue}'}`)
                        .then(res => res.json())
                        .then(json => json.data)
                        .then(data => {
                            data.forEach(t => {
                                result.push({
                                    label: t.teacher_name,
                                    value: t.teacher_id,
                                })
                            })
                        })
                    return result;
                }
            }

            return FetchData();
        }
    };

    //Change event handler
    const navigate = useNavigate();
    const changeSelectHandler = (e) => {
        navigate(`/${way}/${e.value}`)
        setDefaultValue(e);
        setDefaultOptions([e]);
    }

    return (
        <AsyncSelect
            value={defaultValue}
            defaultOptions={defaultOptions}
            loadOptions={loadOptions}
            onChange={changeSelectHandler}
            placeholder={<div>{way == "groups" ? " ..." : " ..."}</div>}
            noOptionsMessage={() => way == "groups" ? '  !' : '  !'}
            loadingMessage={() => '...'}
            className='navigation__select-container'
            classNamePrefix="select"
            isLoading={isLoading}
        />
    )
}

export default ScheduleSelect;