import Lesson from "./Lesson";

function ScheduleColumn({filter, setFilter, weekday, lessons, funMode}) {
    return (
        <div className="table__column">
            <div className="table__weekday">
                <span>{weekday}</span>
            </div>
            {
                lessons.length ? lessons.map(lesson => {
                    let {lesson_id, lesson_number, day_number} = lesson;
                    return (<Lesson setFilter={setFilter}
                                    filter={filter}
                                    funMode={funMode}
                                    key={`${day_number}_${lesson_number}_${lesson_id || ""}`}
                                    data={lesson}/>)
                }) : <></>
            }
        </div>
    )
}

export default ScheduleColumn;